:root {
  --primary-color: #ffcb10;
  --secondary-color: #00d8b4;
  --background: #181818;
}

::selection {
  background-color: var(--background);
  color: #fff;
  text-shadow: none;
}

html {
  font-size: 1vw;
}

body {
  background-color: var(--background);
  color: #fff;
  height: 100vh;
}

.o-page {
  width: 80%;
  margin: 0 auto;
  padding-top: 123px;
  font-family: Silkscreen, Arial, Helvetica, sans-serif;
}

o-layout {
  margin: 0 auto;
}

.o-layout_item {
  width: 49%;
}

.o-layout_item img {
  width: 100%;
}

.o-layout_item p {
  text-align: right;
  font-size: 2.5rem;
  line-height: 1.34;
}

p.o-text {
  text-align: left;
}

.o-background {
  background-color: var(--primary-color);
  height: 129px;
}

.o-header {
  display: inline-block;
}

.o-title {
  text-align: center;
  color: var(--secondary-color);
  padding: 25px 0;
  font-size: 5rem;
}

@media (max-width: 699px) {
  .o-page {
    width: 90%;
  }
}

.l-header {
  padding: 40px 0 20px;
  font-size: 3rem;
}

.l-link-item {
  padding: 20px 0;
}

.l-link-item h2 {
  padding: 0 0 10px;
  font-size: 1.5em;
}

.l-link-item a {
  color: var(--secondary-color);
  padding: 0 0 10px;
  text-decoration: none;
  display: block;
}

.l-link-item p {
  line-height: 1.2;
}

.l-link-item:nth-child(odd) {
  text-align: right;
}

.l-link-item:nth-child(odd) a {
  color: var(--primary-color);
}

.l-link-item a:hover {
  color: #fff;
}

.l-text {
  width: 75%;
  display: inline-block;
}

#categories, #links {
  width: 90%;
}

.e-container {
  grid-template-columns: 25% 25% 25% 25%;
  display: grid;
}

.episode {
  max-width: 100%;
  margin: 6px;
}

.e-thumbnail {
  margin: 0;
  position: relative;
}

.e-thumbnail img {
  width: 100%;
}

.e-thumbnail-gradient {
  background-image: linear-gradient(0deg, #181818cc 5%, #0000 95%);
  height: 65px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: -.5px;
}

.e-data .e-title {
  font-size: 16px;
  font-weight: 500;
}

.e-runtime {
  color: #a3a3a3;
  font-size: 16px;
  line-height: 27px;
}

.e-synopsis {
  color: #a3a3a3;
  font-family: Kubik, Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 15px;
}

#categories h2 {
  font-size: 30px;
  line-height: 65px;
}

@media (max-width: 1439px) {
  .e-container {
    grid-template-columns: 33.333% 33.333% 33.333%;
  }
}

@media (max-width: 1023px) {
  .e-container {
    grid-template-columns: 50% 50%;
  }
}

@media (max-width: 599px) {
  .e-container {
    grid-template-columns: 100%;
  }
}

.f-container, .f-item {
  grid-template-columns: 50% 50%;
  display: grid;
}

.f-thumbnail {
  width: 250px;
}

.f-thumbnail img {
  width: 100%;
}

.hidden-content {
  opacity: 0;
  transition: opacity .3s;
  display: none;
}

.hover-text:after {
  content: "";
  color: #fff;
  background-color: #000c;
  padding: 5px;
  display: none;
  position: absolute;
}

.hover-text:hover:after {
  content: "¡Texto adicional que aparece al pasar el cursor sobre 'ejemplo'!";
  display: block;
}

.c-header_list_item {
  vertical-align: top;
  font-family: Silkscreen, Arial, Helvetica, sans-serif;
  font-size: 16px;
  display: inline-block;
}

.c-header_list_item:nth-child(2) {
  padding: 0 18vw;
}

.c-header {
  z-index: 50;
  background-color: var(--background);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.c-header_nav {
  margin: 0 auto;
}

.c-header_container {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  display: flex;
}

.c-header_list_item a {
  color: var(--secondary-color);
  text-transform: uppercase;
  text-decoration: none;
}

@media (max-width: 999px) {
  .c-header_list_item:nth-child(2) {
    padding: 0 10vw;
  }
}

@media (max-width: 699px) {
  .c-header_list_item:nth-child(2) {
    padding: 0;
  }

  .c-header_list_item {
    text-align: right;
    display: block;
  }

  .c-header_nav {
    margin: 0 25px 0 0;
  }
}



